import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as lendingCriteriaActions from "../../actions/lendingCriteriaActions";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import LendingCriteriaPage from "./LendingCriteriaContact";
import { Navigation } from "../../lib";

class LendingCriteriaContainer extends React.Component {
  componentDidMount() {
    this.props.actions.getParameterListByContact(
      this.props.match.params.contact_id
    );
    this.props.navActions.updateNavTitle("Lending Criteria for Contact Name");
  }

  routeToPreviousPage() {
    Navigation.goBack();
  }

  render() {
    return (
      <div>
        <Row>
          <Row className="lc-sub-header">
            <Col lg="12" md="12" sm="12">
              <div
                onClick={this.routeToPreviousPage.bind(this)}
                className="back-arrow"
              >
                <img src="/images/icons/arrow-left.png" alt="arrow-left" />
              </div>
            </Col>
          </Row>
          <Row>
            <LendingCriteriaPage
              isContactPage={true}
              lendingPrograms={
                this.props.lendingCriteria.lendingList &&
                this.props.lendingCriteria.lendingList.lending_programs
              }
              loading={this.props.lendingCriteria.loading}
              authentication={JSON.parse(localStorage.getItem("data"))}
              noLendingList={this.props.lendingCriteria.noLendingList}
            />
          </Row>
        </Row>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    lendingCriteria: state.lendingCriteria,
    title: state.navTitle,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    navActions: bindActionCreators(navigationTitleActions, dispatch),
    actions: bindActionCreators(lendingCriteriaActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LendingCriteriaContainer);
